<script>
	export default {
		data() {
			return {
				currentUser: '',
				serviceTypes: ['‰∏âÂåÖ', 'Áª¥‰øÆ', '‰øùÂÖª', 'ÊäÄÊîπ', 'ÂÖ∂‰ªñ'],
				faultCategories: ['ÂèëÂä®Êú∫', 'Ê∂≤ÂéãÁ≥ªÁªü', 'Ë°åËµ∞‰º†Âä®Á≥ªÁªü', 'ÁîµÊ∞îÁ≥ªÁªü', 'ÊèíÂÄºÁ≥ªÁªü', 'ÂÖ∂‰ªñ'],
				formData: {
					orderNo: '', // Auto-gen
					customer: {
						name: '',
						phone: '',
						address: '',
						usageType: 'Ëá™Áî®',
						distributorName: ''
					},
					product: {
						machineNo: '',
						engineNo: '',
						productionDate: '',
						platePhoto: '',
						model: ''
					},
					service: {
						type: '',
						faultCategory: '',
						faultDesc: '',
						handleDesc: '',
						parts: [],
						sitePhotos: []
					},
					confirm: {
						machineUserPhoto: ''
					}
				}
			}
		},
		onLoad() {
			const userInfo = uni.getStorageSync('userInfo');
			this.currentUser = userInfo ? userInfo.name : 'Êú™Áü•Áî®Êà∑';
			
			// Generate Order No (Mock)
			const dateStr = new Date().toISOString().slice(0,10).replace(/-/g, '');
			this.formData.orderNo = 'JS' + dateStr + Math.floor(Math.random() * 1000);
			
			// Check Draft
			this.checkDraft();
		},
		onUnload() {
			// Auto save draft logic could go here or on every change
			this.saveDraft();
		},
		methods: {
			checkDraft() {
				const draft = uni.getStorageSync('order_draft');
				if (draft) {
					uni.showModal({
						title: 'ÂèëÁé∞ËçâÁ®ø',
						content: 'ÊòØÂê¶ÊÅ¢Â§ç‰∏äÊ¨°Êú™Êèê‰∫§ÁöÑËçâÁ®øÔºü',
						success: (res) => {
							if (res.confirm) {
								this.formData = draft;
							} else {
								uni.removeStorageSync('order_draft');
							}
						}
					});
				}
			},
			saveDraft() {
				// Only save if some data exists
				if (this.formData.customer.name) {
					uni.setStorageSync('order_draft', this.formData);
				}
			},
			onUsageChange(e) { this.formData.customer.usageType = e.detail.value; },
			onDateChange(e) { this.formData.product.productionDate = e.detail.value; },
			onServiceTypeChange(e) { this.formData.service.type = this.serviceTypes[e.detail.value]; },
			onFaultCatChange(e) { this.formData.service.faultCategory = this.faultCategories[e.detail.value]; },
			
			addPart() {
				this.formData.service.parts.push({ name: '', count: 1 });
			},
			
			chooseImage(type) {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						const path = res.tempFilePaths[0];
						if (type === 'plate') {
							this.formData.product.platePhoto = path;
							// Mock OCR call here
							uni.showToast({ title: 'Ê≠£Âú®ËØÜÂà´...', icon: 'loading' });
							setTimeout(() => {
								this.formData.product.model = 'OCR-Model-X100'; // Mock result
								uni.hideToast();
							}, 1000);
						} else if (type === 'confirm') {
							this.formData.confirm.machineUserPhoto = path;
						} else if (type === 'site') {
							this.formData.service.sitePhotos.push(path);
						}
					}
				})
			},
			
			submitOrder() {
				// Validate
				if (!this.formData.customer.name || !this.formData.product.machineNo) {
					uni.showToast({ title: 'ËØ∑ÂÆåÂñÑÂøÖÂ°´‰ø°ÊÅØ', icon: 'none' });
					return;
				}
				
				uni.showModal({
					title: 'Á°ÆËÆ§Êèê‰∫§',
					content: 'Êèê‰∫§ÂêéÂ∞ÜÊó†Ê≥ï‰øÆÊîπÔºåÊòØÂê¶Á°ÆËÆ§Ôºü',
					success: (res) => {
						if (res.confirm) {
							// TODO: Cloud Submit
							console.log('Submitting:', this.formData);
							uni.showLoading({ title: 'Êèê‰∫§‰∏≠' });
							setTimeout(() => {
								uni.hideLoading();
								uni.removeStorageSync('order_draft'); // Clear draft
								uni.showToast({ title: 'Êèê‰∫§ÊàêÂäü' });
								setTimeout(() => uni.navigateBack(), 1500);
							}, 1500);
						}
					}
				})
			}
		}
	}
</script>

